"use client";
import { useState, useEffect } from "react";
import { BiSearch, BiCheck, BiX } from "react-icons/bi";
import { FiMail, FiPhone } from "react-icons/fi";
import Image from "next/image";
import useAdminStore from "@/store/adminStore";
import toast from "react-hot-toast";

export default function VolunteerRequestTable() {
  const { users, isLoading, getUsers, updateUserStatus } = useAdminStore();
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchVolunteers = async () => {
      try {
        // Fetch users with volunteer role
        await getUsers({ role: 'volunteer', limit: 100 });
      } catch (error) {
        console.error("Failed to load volunteers:", error);
        toast.error("Failed to load volunteers");
      }
    };
    fetchVolunteers();
  }, [getUsers]);

  const handleActivate = async (userId) => {
    try {
      console.log("Activating volunteer:", userId);
      await updateUserStatus(userId, { isActive: true });
      toast.success("Volunteer activated successfully!");
      
      // Refresh data after activation
      setTimeout(() => {
        getUsers({ role: 'volunteer', limit: 100 });
      }, 500);
    } catch (error) {
      console.error("Activate error:", error);
      const errorMessage = error.response?.data?.message || error.message || "Failed to activate volunteer";
      toast.error(errorMessage);
    }
  };

  const handleBlock = async (userId) => {
    if (!confirm("Are you sure you want to block this volunteer?")) {
      return;
    }
    
    try {
      console.log("Blocking volunteer:", userId);
      await updateUserStatus(userId, { isActive: false });
      toast.success("Volunteer blocked successfully!");
      
      // Refresh data after blocking
      setTimeout(() => {
        getUsers({ role: 'volunteer', limit: 100 });
      }, 500);
    } catch (error) {
      console.error("Block error:", error);
      const errorMessage = error.response?.data?.message || error.message || "Failed to block volunteer";
      toast.error(errorMessage);
    }
  };

  const filteredRequests = users.filter(user => {
    const search = searchTerm.toLowerCase();
    const volunteerDetails = user.volunteerDetails || {};
    return (
      user.name?.toLowerCase().includes(search) ||
      user.email?.toLowerCase().includes(search) ||
      user.phone?.includes(search) ||
      volunteerDetails.nationality?.toLowerCase().includes(search) ||
      volunteerDetails.occupation?.toLowerCase().includes(search) ||
      volunteerDetails.communicationAddress?.toLowerCase().includes(search)
    );
  });

  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredRequests.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredRequests.length / entriesPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  // Helper function to format date
  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    try {
      const date = new Date(dateString);
      return date.toISOString().split('T')[0]; // YYYY-MM-DD format
    } catch {
      return dateString;
    }
  };

  // Helper function to get image URL
  const getImageUrl = (imagePath) => {
    if (!imagePath) return null;
    if (imagePath.startsWith('http')) return imagePath;
    const apiUrl = process.env.NEXT_PUBLIC_API_URL?.replace('/api', '') || 'http://localhost:5000';
    return `${apiUrl}${imagePath.startsWith('/') ? imagePath : '/' + imagePath}`;
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Volunteer Request</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li><a href="/admin/volunteer-requests" className="hover:underline text-gray-600 hover:text-blue-600">Volunteer Rqst</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Volunteer Request Data</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Volunteer Request</h2>

        <div className="flex items-center justify-between mb-6 flex-wrap gap-4">
          <div className="flex items-center gap-2">
            <select 
              value={entriesPerPage} 
              onChange={(e) => { setEntriesPerPage(Number(e.target.value)); setCurrentPage(1); }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-gray-600 font-medium">entries per page</span>
          </div>
          <div className="relative">
            <input 
              type="text" 
              placeholder="Search..." 
              value={searchTerm} 
              onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1); }}
              className="border border-gray-300 rounded-lg pl-10 pr-4 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"
            />
            <BiSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
            <p className="text-gray-600 mt-4">Loading volunteers...</p>
          </div>
        ) : (
          <>
            {/* Table with horizontal scroll */}
            <div className="overflow-x-auto -mx-6 px-6">
              <div className="inline-block min-w-full align-middle">
                <div className="overflow-hidden">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gradient-to-r from-green-50 to-blue-50">
                      <tr>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Name</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">DOB</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Mob No.</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Email</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Nationality</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">C.Address</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Occupation</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Designation</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider min-w-[200px]">Message</th>
                        <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Identity Proof</th>
                        <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700 uppercase tracking-wider whitespace-nowrap">Action</th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {currentEntries.length > 0 ? (
                        currentEntries.map((volunteer, index) => {
                          const volunteerDetails = volunteer.volunteerDetails || {};
                          const identityProofUrl = getImageUrl(volunteerDetails.identityProof);
                          
                          return (
                            <tr key={volunteer._id} className={`hover:bg-gray-50 transition-colors ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'}`}>
                              {/* Name */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                                {volunteer.name || 'N/A'}
                              </td>
                              
                              {/* DOB */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                {formatDate(volunteerDetails.dateOfBirth)}
                              </td>
                              
                              {/* Mob No. */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                <div className="flex items-center gap-2">
                                  <FiPhone size={14} className="text-gray-400" />
                                  {volunteer.phone || 'N/A'}
                                </div>
                              </td>
                              
                              {/* Email */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                <div className="flex items-center gap-2">
                                  <FiMail size={14} className="text-gray-400" />
                                  <span className="truncate max-w-[200px]" title={volunteer.email}>
                                    {volunteer.email || 'N/A'}
                                  </span>
                                </div>
                              </td>
                              
                              {/* Nationality */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                {volunteerDetails.nationality || 'N/A'}
                              </td>
                              
                              {/* C.Address */}
                              <td className="px-4 py-3 text-sm text-gray-700 max-w-[200px]">
                                <div className="truncate" title={volunteerDetails.communicationAddress}>
                                  {volunteerDetails.communicationAddress || volunteer.address?.street || 'N/A'}
                                </div>
                              </td>
                              
                              {/* Occupation */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                {volunteerDetails.occupation || 'N/A'}
                              </td>
                              
                              {/* Designation */}
                              <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-700">
                                {volunteerDetails.designation || 'N/A'}
                              </td>
                              
                              {/* Message */}
                              <td className="px-4 py-3 text-sm text-gray-700 min-w-[200px] max-w-[300px]">
                                <div className="line-clamp-3" title={volunteerDetails.whyVolunteer}>
                                  {volunteerDetails.whyVolunteer || 'N/A'}
                                </div>
                              </td>
                              
                              {/* Identity Proof */}
                              <td className="px-4 py-3 whitespace-nowrap">
                                {identityProofUrl ? (
                                  <div className="relative w-24 h-16">
                                    <Image
                                      src={identityProofUrl}
                                      alt="Identity Proof"
                                      fill
                                      className="object-cover rounded border border-gray-200"
                                      sizes="96px"
                                    />
                                  </div>
                                ) : (
                                  <span className="text-xs text-gray-400">No image</span>
                                )}
                              </td>
                              
                              {/* Action */}
                              <td className="px-4 py-3 whitespace-nowrap text-center">
                                <div className="flex gap-2 justify-center">
                                  {/* Activate Button - Always visible */}
                                  <button 
                                    onClick={() => handleActivate(volunteer._id)}
                                    disabled={volunteer.isActive}
                                    className={`flex items-center gap-1 px-3 py-1.5 text-xs font-medium rounded-lg transition-colors whitespace-nowrap ${
                                      volunteer.isActive 
                                        ? 'bg-gray-300 text-gray-500 cursor-not-allowed' 
                                        : 'bg-green-600 text-white hover:bg-green-700'
                                    }`}
                                  >
                                    <BiCheck size={16} />
                                    Activate
                                  </button>
                                  
                                  {/* Block Button - Always visible */}
                                  <button 
                                    onClick={() => handleBlock(volunteer._id)}
                                    disabled={!volunteer.isActive}
                                    className={`flex items-center gap-1 px-3 py-1.5 text-xs font-medium rounded-lg transition-colors whitespace-nowrap ${
                                      !volunteer.isActive 
                                        ? 'bg-gray-300 text-gray-500 cursor-not-allowed' 
                                        : 'bg-red-600 text-white hover:bg-red-700'
                                    }`}
                                  >
                                    <BiX size={16} />
                                    Block
                                  </button>
                                </div>
                              </td>
                            </tr>
                          );
                        })
                      ) : (
                        <tr>
                          <td colSpan="11" className="px-4 py-12 text-center text-gray-500">
                            <div className="text-4xl mb-2">ðŸ¤</div>
                            {isLoading ? 'Loading volunteers...' : 'No volunteers found'}
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="mt-6 flex items-center justify-between">
                <div className="text-sm text-gray-700">
                  Showing {indexOfFirstEntry + 1} to {Math.min(indexOfLastEntry, filteredRequests.length)} of {filteredRequests.length} entries
                </div>
                <div className="flex gap-2">
                  <button
                    onClick={() => paginate(currentPage - 1)}
                    disabled={currentPage === 1}
                    className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Previous
                  </button>
                  <div className="flex gap-1">
                    {[...Array(totalPages)].map((_, i) => {
                      const page = i + 1;
                      if (page === 1 || page === totalPages || (page >= currentPage - 1 && page <= currentPage + 1)) {
                        return (
                          <button
                            key={page}
                            onClick={() => paginate(page)}
                            className={`px-4 py-2 rounded-lg text-sm font-medium ${
                              currentPage === page
                                ? 'bg-green-600 text-white'
                                : 'border border-gray-300 text-gray-700 hover:bg-gray-50'
                            }`}
                          >
                            {page}
                          </button>
                        );
                      } else if (page === currentPage - 2 || page === currentPage + 2) {
                        return <span key={page} className="px-2 py-2 text-gray-500">...</span>;
                      }
                      return null;
                    })}
                  </div>
                  <button
                    onClick={() => paginate(currentPage + 1)}
                    disabled={currentPage === totalPages}
                    className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Next
                  </button>
                </div>
              </div>
            )}
          </>
        )}
      </div>
    </>
  );
}
